.content{
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
        min-width: 190px;
             display: flex;
             img{
                 margin-right: 20px;
                 object-fit: contain;
             }
    }
}
.city-selector,
.country-selector 
{
    background-color: rgba(0,0,0,0.3);
    padding: 8px 15px;
    border-radius: 10px;
    box-sizing: border-box;
    color: white;
    border: transparent 1px solid;
    display: block;
    margin: 10px auto;
    width: 60%;

}
::placeholder{
    color: white
}
.search-button{
padding: 8px 15px;
border-radius: 10px;
box-sizing: border-box;
color: white;
border: transparent 1px solid;
display: block;
margin: 10px auto;
width: 30%;
background: #3e7140;
}